	module.exports = {"data":{"markdownRemark":{"html":"<h2>Go</h2>\n<p><img src=\"https://golang.org/doc/gopher/bumper320x180.png\" alt=\"Go bumper\"></p>\n<p><strong>Go</strong> (or <strong>golang</strong>) is a programming language created at Google in 2007 by Robert Griesemer, Rob Pike, and Ken Thompson. It is a compiled, statically-typed language in the tradition of Algol and C. It has garbage collection, limited structural typing, memory safety, and CSP-style concurrent programming features added. The compiler and other language tools originally developed by Google are all free and open source. Its popularity is increasing fast. It is a great choice for building web applications.</p>\n<p>For more information head to <a href=\"https://golang.org/\">Go’s Homepage</a></p>\n<p>Want a quick <a href=\"https://tour.golang.org/welcome/1\">Tour of Go?</a></p>\n<h2>Pre-Installations:</h2>\n<hr>\n<h4>Install Golang with Homebrew:</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>$ brew update\n$ brew <span class=\"token function\">install</span> golang\n</code></pre>\n      </div>\n<h4>When installed, try to run go version to see the installed version of Go.</h4>\n<h3>Setup the workspace:</h3>\n<hr>\n<h5>Add Environment variables:</h5>\n<p>First, you’ll need to tell Go the location of your workspace.</p>\n<p>We’ll add some environment variables into shell config. One of does files located at your home directory bash_profile, bashrc or .zshrc (for Oh My Zsh Army)</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>$ <span class=\"token function\">vi</span> .bashrc\n</code></pre>\n      </div>\n<p>then add those lines to export the required variables</p>\n<h4>This is actually your .bashrc file</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">export</span> GOPATH<span class=\"token operator\">=</span><span class=\"token variable\">$HOME</span>/go-workspace <span class=\"token comment\" spellcheck=\"true\"># don't forget to change your path correctly!</span>\n<span class=\"token function\">export</span> GOROOT<span class=\"token operator\">=</span>/usr/local/opt/go/libexec\n<span class=\"token function\">export</span> PATH<span class=\"token operator\">=</span><span class=\"token variable\">$PATH</span><span class=\"token keyword\">:</span><span class=\"token variable\">$GOPATH</span>/bin\n<span class=\"token function\">export</span> PATH<span class=\"token operator\">=</span><span class=\"token variable\">$PATH</span><span class=\"token keyword\">:</span><span class=\"token variable\">$GOROOT</span>/bin\n</code></pre>\n      </div>\n<h4>Create your workspace:</h4>\n<hr>\n<h5>Create the workspace directories tree:</h5>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>$ <span class=\"token function\">mkdir</span> -p <span class=\"token variable\">$GOPATH</span> <span class=\"token variable\">$GOPATH</span>/src <span class=\"token variable\">$GOPATH</span>/pkg <span class=\"token variable\">$GOPATH</span>/bin\n<span class=\"token variable\">$GOPATH</span>/src <span class=\"token keyword\">:</span> Where your Go projects / programs are located\n<span class=\"token variable\">$GOPATH</span>/pkg <span class=\"token keyword\">:</span> contains every package objects\n<span class=\"token variable\">$GOPATH</span>/bin <span class=\"token keyword\">:</span> The compiled binaries home\n</code></pre>\n      </div>\n<h3>Quickstart</h3>\n<p>For a quickstart and boilerplate Go project, try <a href='https://www.growthmetrics.io/open-source/alloy' target='_blank' rel='nofollow'>Alloy</a></p>\n<ol>\n<li>\n<p>Clone Alloy repository</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>git clone https://github.com/olliecoleman/alloy\ncd alloy</code></pre>\n      </div>\n</li>\n<li>\n<p>Install the dependencies</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>glide install\nnpm install</code></pre>\n      </div>\n</li>\n<li>\n<p>Start the development server</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>go install\nalloy dev</code></pre>\n      </div>\n</li>\n<li>Visit website at <code>http://localhost:1212</code></li>\n</ol>\n<p><em>Alloy uses Node, NPM, and Webpack</em></p>\n<h3>Go Playground</h3>\n<p>  <a href=\"https://play.golang.org/\">Go Playground</a></p>\n<p>Learning how to install go on your local machine is important, but if want to start playing with go right in your browser, then Go Playground is the perfect sandbox to get started right away! To learn more about the Go Playground see their article titled <a href=\"https://blog.golang.org/playground\">Inside the Go Playground</a></p>","fields":{"slug":"/go/"},"frontmatter":{"title":"Go"}}},"pathContext":{"slug":"/go/"}}

